* {
  margin: 0;
}

body {
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  padding: 0;
  display: flex;
}

.nav__list__item {
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
}

.nav__link {
  color: #000;
  align-content: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: inline-block;
}

.nav__list__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--main-color);
}

a.is-active {
  color: var(--main-color);
  position: relative;
}

a.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 10px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.logo__image {
  width: 40px;
  height: 40px;
}
